import {
  SourceSelectorSelectedPackageInfo,
  SourceSelectorSelectedSourceInfo,
} from "../sourceSelector.types"

function getIsSelectionSource(
  selection: SourceSelectorSelectedSourceInfo | SourceSelectorSelectedPackageInfo,
): selection is SourceSelectorSelectedSourceInfo {
  return !!(selection as SourceSelectorSelectedSourceInfo).packageIDs
}

export { getIsSelectionSource }
